.back {
  min-height: 100vh;
  width: 100%;
  padding: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}
.back .card {
  box-sizing: border-box;
  display: grid;
  place-items: center;
  text-align: center;
  border-radius: 20px;
  padding: 30px;
  width: 100%;
  max-width: 500px;
  background-color: white;
}
.back .card h1 {
  font-size: 18px;
}
.back .card p {
  line-height: 25px;
  color: #7a7a7a;
}
.back .card button {
  width: 40%;
  padding: 15px 30px;
  border-radius: 30px;
  font-size: max(14px, 1vw);
  font-weight: 600;
  border: none;
  color: white;
  background-color: #3cb3ab;
  cursor: pointer;
}
.back .card button:hover {
  background-color: #1d6863;
}/*# sourceMappingURL=thanks.css.map */